.maslider-section {

	//border-top: 1px solid #C4C4C4;
	&:hover {
		.maslider-arrow {
			opacity: 1;
		}
	}
}

.maslider {
	&--type-home-lessons {
		.maslider-arrow {
			background: #FFFFFF;
			backdrop-filter: none;
			border: 1px solid #AFB4BF;
		}
	}
}

.maslider-slide {
	max-width: 848px;
	width: 100%;
	padding: 0 3px 0;
	height: 400/842 * 100%;

	&__inner {
		display: block;
		width: 100%;
	}

	&--video {
		.maslider-slide__inner {
			&::after {
				position: relative;
				width: 100%;
				z-index: 31;
				opacity: 0;
				transition: all .4s ease;
				background: rgba(255, 255, 255, .1);
				backdrop-filter: blur(5px);
			}

			&::before {
				position: absolute;
				z-index: 32;
				top: 50%;
				left: 50%;
				display: block;
				width: 93px;
				height: 93px;
				content: '';
				transition: all .4s ease;
				transform: translate(-50%, -50%);
				background: url(../img/play-vid-big.svg) no-repeat center center;
				background-size: contain;
				opacity: 0;
			}
			@media screen and (min-width: 1151px) {
				&:hover {

					&::after,
					&::before {
						opacity: 1;
					}
				}
			}
		}
	}
}

.maslider-slide__title {
	position: absolute;
	width: 100%;
	height: 100%;
	padding: 21px 56px 20px;

	top: 0;
	left: 0;
	font-size: 35px;
	line-height: 26px;
	letter-spacing: -0.2px;
	display: flex;
	align-items: center;
	color: #363B42;
	z-index: 30;

	span {
		display: block;
	}
}

.maslider-slide__inner {
	position: relative;
	overflow: hidden;


	&::after {
		content: '';
		padding-top: 398/842 * 100%;
		display: block;
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}

	/*@include scalepic();*/
}

.maslider-arrow {
	position: absolute;
	bottom: 20px;
	width: 37px;
	height: 37px;
	box-sizing: border-box;


	background: rgba(255, 255, 255, 0.5);
	backdrop-filter: blur(10px);



	border: 1px solid #AFB4BF;
	box-sizing: border-box;
	-webkit-mask-image: -webkit-radial-gradient(white, #000);
	border-radius: 500px;
	transform: rotate(-180deg);
	display: flex;
	align-items: center;
	justify-content: center;
	-webkit-mask-image: -webkit-radial-gradient(white, #000);
	border-radius: 50%;
	z-index: 100;
	cursor: pointer;
	transition: all .4s ease;
	opacity: 0;

	&--prev {
		left: 22px;
	}

	&--next {
		right: 22px;

		svg {
			transform: rotate(180deg);
		}
	}

	&,
	svg path {
		transition: all .4s ease;
	}

	svg {
		display: block;
		height: auto;
		width: 7px;

		path {}
	}

	/*&:hover{
		border-color:  #6974A9;
		svg path{
			fill: #6974A9;
		}
	}*/
}