@media screen and (max-width: 1150px) {
	.get-materials-hr-before {

		margin-top: 76px;

		&+.get-materials {
			&--has-title-small {
				margin-top: 52px;
			}

			&--has-title-big {}

		}
	}

	.get-materials-hr-after {
		&+.kspc-simple-text--has-title {
			margin-top: 85px;
		}

		&+.kspc-tags-and-share--after-quote-right {}
	}

	.get-materials {}

	.get-materials-title {
		font-weight: 600;
		font-size: 19px;
		line-height: 24px;
	}

	.get-materials-title-small {

		font-weight: 600;
		font-size: 15px;
		line-height: 24px;

		&+.get-materials-row {
			margin-top: 29px;
		}
	}

	.get-materials-title-big {
		font-size: 24px;
		line-height: 39px;
		letter-spacing: normal;
	}

	.get-materials-subtitle {
		font-size: 15px;
		line-height: 24px;
		margin-top: 4px;

		&+.get-materials-row {
			margin-top: 30px;
		}
	}

	.get-materials .get-materials-calnedar{
		margin-top: 12px;
		padding-right: 2px;
		.kspc-event-calendar__item{
			margin-left: 22px;
		}
	}


	.get-materials-row {
		margin-top: 31px;

		&__left {
			width: 337px;
			max-width: calc(100% - 359px);
		}

		&__right {}

		.music-form-input.js-need-validate {
			&--email {}
		}

		.btn--get-materials {
			width: 206px;
			font-weight: 500;
			font-size: 13px;
			line-height: 21px;
		}
	}
}

@media screen and (max-width: 749px) {
	.get-materials-hr-before {
		margin-top: vw(86);

		&+.get-materials {
			&--has-title-small {
				margin-top: vw(51);
			}

			&--has-title-big {
				margin-top: vw(55);
			}

		}
	}

	.get-materials-hr-after {
		margin-top: vw(58);

		&+.kspc-simple-text--has-title {
			margin-top: vw(85);
		}

		&+.kspc-tags-and-share--after-quote-right {
			margin-top: vw(58);
		}
	}

	.get-materials {
		margin-top: vw(46);
	}

	.get-materials-title {
		font-weight: 600;
		font-size: vw(19);
		line-height: vw(24);
	}

	.get-materials-title-small {

		font-weight: 600;
		font-size: vw(15);
		line-height: vw(24);

		&+.get-materials-row {
			margin-top: vw(29);
		}
	}

	.get-materials-title-big {
		font-size: vw(24);
		line-height: vw(24);
		font-weight: normal;
		span{
			white-space: nowrap;
		}
	}

	.get-materials-subtitle {
		font-size: vw(15);
		line-height: vw(24);
		margin-top: vw(10);

		&+.get-materials-row {
			margin-top: vw(29);
		}
	}

	.get-materials .get-materials-calnedar{
		margin-top: vw(12);
		padding-right: 0;
		margin-left: 0;
		justify-content: space-between;
		.kspc-event-calendar__item{
			margin-left: 0;
		}
		+.get-materials-row{
			margin-top: vw(32);
		}
	}


	.get-materials-row {
		flex-direction: column;
		align-items: center;
		margin-top: vw(30);

		&__left {
			width: 100%;
			max-width: 100%;
			margin-right: 0;
		}

		&__right {
			margin-top: vw(23);
		}

		.music-form-input.js-need-validate {
			&--email {}
		}

		.btn--get-materials {
			width: vw(206);
			font-weight: 500;
			font-size: vw(13);
			line-height: vw(21);
		}
	}
}