@media screen and (max-width: 1150px) {
	.new-article {
		padding-top: 54px;
		padding-bottom: 53px;

		&--videos {
			padding-bottom: 45px;
		}

		&__container {
			max-width: 100%;
			width: 100%;
		}

		&__header {
			margin-bottom: 0;
		}

		&__title {
			letter-spacing: -.03em;
			margin-bottom: 0;
			margin-left: 0;
		}

		&__link-more {
			margin-top: 0;
			margin-top: 8px;
		}

		&__slider {
			margin: 0;
			margin-top: 29px;

			//margin: -18px;
			.slick-list {
				margin: 0;

				&::before,
				&::after {
					display: none;
				}
			}
		}

		&__slider-card {
			width: 273px;
			margin: 0;
			margin-right: 36px;
			padding: 0;

			&--videos {
				width: 403px;
			}
		}

		&__card-img-wrapper {
			width: 100%;
			max-width: 100%;
			position: relative;
			margin-bottom: 20px;

			&::after {
				content: '';
				display: block;
				padding-top: 100%;
			}

			&--videos {
				margin-bottom: 20px;

				&::after {
					padding-top: 62%;
				}

				&+.new-article__card-title {
					margin-bottom: 11px;
				}

			}
		}

		&__card-img {}

		&__card-title {
			margin-bottom: 9px;
			font-weight: 600;
			font-size: 19px;
			line-height: 26px;
			/* identical to box height, or 187% */


			color: $dark;

		}

		&__theme-wrapper {
			margin-bottom: 8px;
		}

		&__theme-link {
			font-weight: 400;
			font-size: 13px;
			line-height: 18px;
			//text-transform: uppercase;
			color: #25378D;
			margin-right: 11px;
		}

		&__theme-date {
			font-weight: 400;
			font-size: 13px;
			line-height: 18px;
			//text-transform: uppercase;
			color: #AFB4BF;
			margin-right: 11px;
		}

		&__theme-icon {
			width: 14px;
			height: 13px;
		}

		&__desc {
			font-style: normal;
			font-weight: 400;
			font-size: 13px;
			line-height: 22px;
			letter-spacing: -.06px;
			max-width: 100%;
			width: 100%;
			margin-right: 0;

			color: $dark;
		}

		.slick-dots {
			display: none !important;
		}


		&--videos {
			.card__title {
				font-size: 19px;
				line-height: 26px;
			}

			.new-article__theme-wrapper {
				margin-bottom: 0;
			}
		}
	}




	.videos-duration {
		opacity: 1;
		padding: 2px 11px 1px;
		right: 12px;
	}

	.videos-duration-text {

		font-size: 15px;
		line-height: 15px;
		font-weight: 500;

	}


}

@media screen and (max-width: 749px) {

	//767 
	.new-article {
		padding-top: vw(47);
		padding-bottom: vw(52);

		&--videos {padding-bottom: 15.6vw;
		}


		&__container {
			max-width: 100%;
			width: 100%;
		}

		&__header {
			margin-bottom: vw(7);
		}

		&__title {
			letter-spacing: normal;
			margin-bottom: 0px;
		}

		&__link-more {
			margin-top: vw(5);
			font-size: vw(13);
			line-height: vw(16);
		}

		&__slider {
			margin-top: vw(7);
		}

		&__slider-card {
			padding: 0;
			width: vw(266);
			margin-right: vw(23);
		}

		&__card-img-wrapper {
			position: relative;
			margin-bottom: vw(9);

			&::after {
				content: '';
				display: block;
				padding-top: 100%;
			}

			&--videos {
				margin-bottom: vw(6);

				&::after {
					padding-top: 62%;
				}

				&+.new-article__card-title {
					margin-bottom: vw(4);
					font-weight: 600;
					font-size: vw(15);
					line-height: vw(24);
				}

			}
		}

		&__card-img {}

		&__card-title {
			margin-bottom: vw(3);
			font-size: vw(15);
			line-height: vw(24);

			color: $dark;

		}

		&__theme-wrapper {
			margin-bottom: vw(4);
		}

		&__theme-link {
			font-weight: 400;
			font-size: vw(13);
			line-height: vw(15);
			//text-transform: uppercase;
			color: #25378D;
			margin-right: vw(13);
		}

		&__theme-date {
			font-weight: 400;
			font-size: vw(13);
			line-height: vw(15);
			//text-transform: uppercase;
			color: #9CA1AC;
			margin-right: vw(14);
		}

		&__theme-icon {
			width: vw(13);
			height: vw(13);
		}

		&__desc {
			font-style: normal;
			font-weight: 400;
			font-size: vw(15);
			line-height: vw(24);
			max-height: vw(48);
			/* or 160% */


			/* Font_text_dark */

			color: $dark;
		}

		.slick-dots {
			display: none !important;
		}

		&--videos+.hr-video+&--videos {
			padding-top: vw(41);
		}

		&--videos {
			.card__title {}

			.new-article__theme-wrapper {
				margin-bottom: 0;
			}
		}
	}





	.videos-duration {
		right: vw(9);
		bottom: vw(9);
		border-radius: 50px;
		padding: vw(2) vw(13) vw(1);
	}

	.videos-duration-text {

		font-size: vw(15);
		line-height: vw(15);
		font-weight: 500;

	}


}